@import "@{bowerPath}/bootstrap3/less/variables.less"; // Modify this for custom colors, font-sizes, etc
@icon-font-path: "@{bowerPath}/bootstrap3/dist/fonts/";
@import "@{bowerPath}/bootstrap3/less/glyphicons.less";
@import "@{mockuplessPath}/ui.less";
@import "@{mockupPath}/tooltip/pattern.tooltip.less";
@import "@{mockupPath}/datatables/pattern.datatables.less";

@import "@{bowerPath}/bootstrap3/less/mixins.less";
@import "@{bowerPath}/bootstrap3/less/utilities.less";
@import (reference) "@{bowerPath}/bootstrap3/less/buttons.less";
@import (reference) "@{bowerPath}/bootstrap3/less/button-groups.less";
@import (reference) "@{bowerPath}/bootstrap3/less/badges.less";
@import (reference) "@{bowerPath}/bootstrap3/less/popovers.less";
@import (reference) "@{bowerPath}/bootstrap3/less/pagination.less";
@import (reference) "@{bowerPath}/bootstrap3/less/input-groups.less";
@import (reference) "@{bowerPath}/bootstrap3/less/tables.less";
@import (reference) "@{bowerPath}/bootstrap3/less/dropdowns.less";
@import (reference) "@{bowerPath}/bootstrap3/less/forms.less";
@import (reference) "@{bowerPath}/bootstrap3/less/navbar.less";
@import (reference) "@{bowerPath}/bootstrap3/less/navs.less";
@import (reference) "@{bowerPath}/bootstrap3/less/component-animations.less";
@import (reference) "@{mockuplessPath}/popover.less";

.pat-structure {
    .fade:extend(.fade all) {
    }
    .collapse:extend(.collapse all) {
    }
    .collapsing:extend(.collapsing all) {
    }

    .popover:extend(.popover all) {
    }
    .popover-title:extend(.popover-title all) {
    }
    .popover-content:extend(.popover-content all) {
    }
    .arrow:extend(.arrow all) {
    }

    .table:extend(.table all) {
    }
    .pagination:extend(.pagination all) {
    }

    .btn:extend(.btn all) {
    }
    .btn-default:extend(.btn-default all) {
    }
    .btn-danger:extend(.btn-danger all) {
    }
    .btn-primary:extend(.btn-primary all) {
    }
    .btn-group:extend(.btn-group all) {
    }
    .btn-success:extend(.btn-success all) {
    }
    .input-group:extend(.input-group all) {
    }
    .input-group-addon:extend(.input-group-addon all) {
    }
    .input-group-btn:extend(.input-group-btn all) {
    }
    .dropdown-menu:extend(.dropdown-menu all) {
    }
    .dropdown-toggle:extend(.dropdown-toggle all) {
    }
    .dropdown-header:extend(.dropdown-header all) {
    }
    .divider:extend(.divider all) {
    }

    .table-striped:extend(.table-striped all) {
    }
    .table-bordered:extend(.table-bordered all) {
    }

    .navbar:extend(.navbar all) {
    }
    .navbar-search:extend(.navbar-search all) {
    }
    .form-search:extend(.form-search all) {
    }
    .ui-offset-parent:extend(.ui-offset-parent all) {
    }

    .form-group:extend(.form-group all) {
    }
    .help-block:extend(.help-block all) {
    }
    .checkbox:extend(.checkbox all) {
    }

    .open:extend(.open all) {
    }

    .btn {
        .btn-sm();
    }

    .btn.has-filter {
        .btn-info();
    }
    input.has-filter {
        border: 1px solid @btn-info-border;
    }

    .pagination {
        vertical-align: middle;
    }

    div.pagination {
        margin-left: 10px;
    }

    .navbar {
        position: sticky;
        top: 0;
        z-index: 10;
        background-color: #fafafa;

        border: none;
        min-height: auto;
        margin: 0;

        & > a.btn:last-of-type,
        .btn-group {
            margin-right: 1em;
        }
        #filter {
            width: 20em;
            float: right;

            .btn-queryfilter {
                margin-left: 1em;
            }
        }
        & > a.btn,
        .btn-group,
        #filter {
            margin-bottom: 0.5em;
        }
    }

    table {
        margin-bottom: 0;

        tr {
            td.selection {
                width: 15px;
            }
            td.actionmenu-container {
                a {
                    white-space: nowrap;
                    &,
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
        .fc-breadcrumbs-container {
            .fc-breadcrumbs {
                font-size: 15px;
                padding-top: 2px;
            }
        }
        .plone-item-expired,
        .plone-item-ineffective {
            .badge();
            font-size: 12px;
            color: white;
        }
        .plone-item-expired {
            background-color: @plone-toolbar-private-color;
        }
        .plone-item-ineffective {
            background-color: @plone-toolbar-internal-color;
        }
    }
    .table th,
    .table td {
        padding: 4px;
    }

    .contextmenu {
        display: none;
        position: fixed;
        z-index: 10000;

        ul {
            display: block;
            position: static;
            margin-bottom: 5px;
        }
    }
    .contextmenuactive {
        outline: 1px dashed orange;
    }

    .navbar-search {
        input.form-control,
        #btn-query {
            height: 31px;
        }
    }
    .fc-status {
        display: flex;
        align-items: center;
        margin-bottom: 1em;
        padding: 0.5em;
        color: @plone-link-color;
        .fc-status-text {
            flex-grow: 1;
        }
        .btn {
            margin-left: 1em;
        }
    }

    .popover.upload {
        max-width: 650px;
    }

    .popover.rearrange {
        max-width: 400px;
    }

    .upload-container .upload-area {
        width: 500px;
    }

    li.disabled a {
        pointer-events: none;
    }

    /* button overrides to make it look
       more streamlined */
    #btn-attribute-columns {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        border-right: 0;
    }
    #btn-selected-items,
    #btn-rearrange {
        border-radius: 0;
        border-right: 0;
    }
    #btn-upload {
        margin-left: 0;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    @media all and (max-width: 522px) {
        .navbar #filter {
            float: none;
            width: 100%;
        }
        #btn-attribute-columns {
            display: none;
        }
        #btn-selected-items {
            border-top-left-radius: 3px;
            border-bottom-left-radius: 3px;
            border-left: 1px solid #cccccc;
        }
        table {
            thead {
                th {
                    display: none;
                }
                th.selection,
                th.title,
                th.actions {
                    display: table-cell;
                }
            }
            tbody {
                td {
                    display: none;
                }
                td.selection,
                td.title,
                td.actionmenu-container {
                    display: table-cell;
                }
            }
        }
    }

    .popover {
        .popover-title {
            .remove-all {
                display: inline-block;
                clear: left;
                margin-top: 5px;
                margin-left: 6px;

                &:before {
                    padding-right: 5px;
                }

                &:hover {
                    text-decoration: none;
                }
            }
        }
    }

    .clear {
        clear: both;
    }

    .pattern-pickadate-wrapper
        .pattern-pickadate-date-wrapper
        .pattern-pickadate-date {
        width: 200px;
    }

    .pattern-pickadate-wrapper
        .pattern-pickadate-time-wrapper
        .pattern-pickadate-time {
        width: 120px;
    }

    .itemRow .title a {
        color: #333333;
    }
    .itemRow.folder .title a {
        color: #005580;
    }
    .itemRow.default-page .title > *:first-child:before {
        content: "*";
        color: red;
        position: absolute;
        text-indent: -4px;
    }
}

.popover.selected-items {
    .items {
        max-height: 300px;
        overflow-y: auto;

        .selected-item {
            display: block;
            margin: 0 8px 3px 0;
            border-radius: 6px;
            padding: 4px 6px;

            &:hover {
                background-color: #f2f2f2;
            }

            a:hover {
                text-decoration: none;
            }

            a:before {
                .glyphicon-remove-sign();
                color: #999;

                &:hover {
                    color: #000;
                }
            }
        }
    }
}

.popover.query {
    width: 700px;
    max-width: 700px;
}

#popover-rename {
    .popover-content {
        max-height: 500px;
        overflow-y: scroll;
    }
    input {
        width: 100%;
        height: 24px;
        line-height: 110%;
        font-size: 110%;
    }
    div.item {
        border-bottom: 1px solid #0e6eb0;
        margin-bottom: 5px;
        padding-bottom: 10px;

        /* clearfix */
        display: table;
        clear: both;
        content: "";

        div.form-group {
            float: left;
        }
        img {
            float: left;
            margin-left: 1em;
        }
    }
}

.popover.attribute-columns {
    ul {
        list-style: none;
        margin: 0 0 1ex 0;
        padding: 0;

        li {
            list-style: none;
            cursor: move;
            border: 1px solid #ddd;
            border-bottom: none;
            padding: 3px 7px;

            &:last-child {
                border-bottom: 1px solid #ddd;
            }

            label {
                margin-bottom: 0;

                input[type="checkbox"] {
                    margin-right: 1ex;
                }
            }
        }
    }
}

.order-support tbody tr {
    cursor: move;
}

table.structure-dragging {
    outline: 1px dashed black;
    background-color: white;
    .itemRow {
        .actionmenu-container {
            display: none;
        }
        td {
            padding: 5px;
        }
    }
}
