//*// TOOLBAR EDIT ZONE PLONE //*//

// might get overridden later by plone registry stored variables!
@import url("variables.less");

#edit-zone {
    font-family: @plone-toolbar-font-primary;
    font-weight: 400;
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    width: @plone-left-toolbar;
    height: 100%;
    color: @plone-toolbar-text-color;
    background: @plone-toolbar-bg;
    word-wrap: break-word;
    hyphens: auto;

    a {
        display: block;
        text-decoration: none;
        color: @plone-toolbar-link;
        outline: 0;

        &:focus {
            background: @plone-toolbar-link;
        }

        &:hover {
            background: @plone-toolbar-link;
        }
    }

    /*logo*/

    .plone-toolbar-logo {
        display: table-cell;
        width: @plone-left-toolbar;
        height: 50px;
        padding-right: 18px;
        padding-left: 18px;
        cursor: pointer;
        vertical-align: middle;
        background: darken(@plone-toolbar-link, 10%);

        &:after {
            position: absolute;
            top: 50px;
            left: 25px;
            content: "";
            border-top: 4px solid darken(@plone-toolbar-link, 10%);
            border-right: 4px solid transparent;
            border-left: 4px solid transparent;
        }

        &:hover {
            -webkit-transition: background 0.25s;
            transition: background 0.35s;
            background: @plone-toolbar-link;

            &:after {
                -webkit-transition: border-top 0.25s;
                transition: border-top 0.35s;
                border-top: 4px solid @plone-toolbar-link;
            }
        }
    }

    /*first-level*/

    nav > ul {
        font-family: @plone-toolbar-font-primary;
        font-size: 12px;
        margin: 0;
        padding: 0;
        list-style: none;

        [class^="icon"] {
            font-size: 20px;
        }

        a {
            position: relative;
            overflow: hidden;
            height: 50px;
            color: @plone-toolbar-text-color;

            &:hover > span {
                margin-top: -50px;
                -webkit-transition: margin-top 0.35s;
                transition: margin-top 0.35s;
            }

            &:hover > span.plone-toolbar-caret {
                opacity: 1;
            }

            > span {
                display: block;
                width: @plone-left-toolbar;
                height: 50px;
                text-align: center;

                &:before {
                    line-height: 50px;
                }
            }

            > span + span {
                line-height: 1em;
                display: table-cell;
                width: @plone-left-toolbar;
                height: 50px;
                padding: 0 8px;
                text-align: center;
                vertical-align: middle;
                background: @plone-toolbar-link;
            }
        }

        .scroll-btn {
            position: fixed;
            z-index: 10;
            background-color: black;

            &.up {
                top: 50px;
            }

            &.down {
                bottom: 50px;
            }

            a {
                height: 20px;

                [class^="icon"] {
                    font-size: 12px;
                }

                > span {
                    height: 20px;
                }

                span:before {
                    line-height: 26px;
                }

                span.icon-down:before {
                    line-height: 20px;
                }

                &:hover {
                    background: @plone-toolbar-link;
                    & > span {
                        margin-top: 0;
                        -webkit-transition: none;
                        transition: none;
                    }
                }
            }
        }
        /*states on first level*/

        a.label-state-published > span:before,
        a.label-state-external > span:before {
            color: @plone-toolbar-published-color;
        }

        a.label-state-published > span + span,
        a.label-state-external > span:before {
            background: @plone-toolbar-published-color;
        }

        a.label-state-internally_published > span:before {
            color: @plone-toolbar-internally-published-color;
        }

        a.label-state-internally_published > span + span {
            background: @plone-toolbar-internally-published-color;
        }

        a.label-state-pending > span:before {
            color: @plone-toolbar-pending-color;
        }

        a.label-state-pending > span + span {
            background: @plone-toolbar-pending-color;
        }

        li.active a.label-state-pending > span,
        a.label-state-pending > span:hover {
            color: (#fff - @plone-toolbar-text-color) !important;
        } //contrast

        a.label-state-draft > span:before,
        a.label-state-internal > span:before {
            color: @plone-toolbar-draft-color;
        }

        a.label-state-draft > span + span,
        a.label-state-internal > span + span {
            background: @plone-toolbar-draft-color;
        }

        a.label-state-private > span:before {
            color: @plone-toolbar-private-color;
        }

        a.label-state-private > span + span {
            background: @plone-toolbar-private-color;
        }

        /*second-level*/

        ul {
            font-family: @plone-toolbar-font-secondary;
            font-size: 14px;
            line-height: 30px;
            position: absolute;
            top: 0;
            left: @plone-left-toolbar;
            overflow-y: auto;
            margin: 0;
            padding: 0;
            list-style: none;
            background: @plone-toolbar-submenu-bg;

            //prepare icon

            [class^="icon"] {
                line-height: 0;
                display: inline !important;

                &:before {
                    font-size: 10px;
                    line-height: 18px;
                    position: absolute;
                    top: 0;
                    right: 10px;
                    margin-top: 7px;
                    color: @plone-toolbar-private-color;
                }
            }

            //states

            .state-published:after,
            .state-external:after,
            #workflow-transition-publish:after {
                font-size: 20px;
                position: absolute;
                right: 15px;
                content: "";
                color: @plone-toolbar-published-color;
            }

            .state-internally_published:after,
            #workflow-transition-show_internally:after {
                font-size: 20px;
                position: absolute;
                right: 15px;
                content: "";
                color: @plone-toolbar-internally-published-color;
            }

            .state-pending:after,
            #workflow-transition-submit:after {
                font-size: 20px;
                position: absolute;
                right: 15px;
                content: "";
                color: @plone-toolbar-pending-color;
            }

            .state-draft:after,
            .state-internal:after,
            #workflow-transition-hide:after {
                font-size: 20px;
                position: absolute;
                right: 15px;
                content: "";
                color: @plone-toolbar-draft-color;
            }

            .state-private:after,
            #workflow-transition-reject:after {
                font-size: 20px;
                position: absolute;
                right: 15px;
                content: "";
                color: @plone-toolbar-private-color;
            }

            //submenu styles

            a {
                color: @plone-toolbar-submenu-text-color;

                &:hover {
                    background: @plone-toolbar-link;
                }
            }

            li {
                min-width: @plone-toolbar-submenu-width;
                width: auto;
                white-space: nowrap;

                &:last-child {
                    padding-bottom: 5px;
                }

                > span {
                    line-height: 20px;
                    display: block;
                    height: auto;
                    padding: 5px 15px;
                }

                > a {
                    line-height: 20px;
                    height: auto;
                    padding: 5px 15px;
                }

                &.plone-toolbar-submenu-header,
                &.plonetoolbar-display-view.actionSeparator {
                    margin: 5px 0 0;
                    padding: 5px 0 0;
                    border-top: 1px solid @plone-toolbar-separator-color;

                    &:first-child {
                        margin: 0;
                        border-top: 0;
                    }

                    &:after {
                        font-size: 30px;
                        right: 13px;
                    }

                    > a {
                        font-weight: 400;
                        color: @plone-toolbar-link;

                        &:hover {
                            color: darken(@plone-toolbar-link, 15%);
                        }
                    }

                    > span {
                        font-weight: 400;
                        color: @plone-toolbar-submenu-header-color;
                    }
                }

                &.plonetoolbar-display-view.actionSeparator {
                    margin: 0;
                    > :before {
                        content: "";
                    }
                    > span {
                        padding: 5px 15px;
                    }
                }

                &.plonetoolbar-workfow-transition,
                &.plonetoolbar-content-action,
                &.plonetoolbar-display-view,
                &.plonetoolbar-portlet-manager,
                &.user-action {
                    > :before {
                        position: absolute;
                        left: 15px;
                        content: "";
                        color: @plone-toolbar-link;
                    }

                    > span {
                        height: auto;
                        padding: 5px 15px 5px 30px;
                        color: @plone-toolbar-text-color;
                    }

                    > a {
                        height: auto;
                        padding: 5px 15px 5px 30px;

                        &:hover:before {
                            color: lighten(@plone-toolbar-link, 25%);
                        }

                        &.actionMenuSelected {
                            &:hover {
                                cursor: default;
                                color: @plone-toolbar-text-color;
                                background: none;
                            }

                            &:before {
                                color: @plone-toolbar-text-color;
                            }
                        }
                    }

                    > .actionMenuSelected {
                        font-weight: 500;
                        color: @plone-toolbar-text-color;

                        &:before {
                            color: darken(@plone-toolbar-text-color, 15%);
                        }

                        &:before {
                            margin-left: -3px;
                            content: "" !important;
                        }
                    }
                }
            }
        }

        .plone-toolbar-separator {
            border-bottom: 1px solid @plone-toolbar-separator-color;
        }

        .plone-toolbar-caret {
            position: absolute;
            top: 21px;
            right: 0;
            right: 2px;
            display: inline-block;
            width: 0;
            height: 0;
            margin-top: 0 !important;
            padding: 0;
            vertical-align: middle;
            opacity: 0.67;
            border-top: 4px solid transparent;
            border-bottom: 4px solid transparent;
            border-left: 4px solid;
            background: transparent !important;
        }
    }
}

.plone-toolbar-left {
    // expand fist level list items to full width
    &.plone-toolbar-expanded {
        ul.plone-toolbar-main > li {
            width: @plone-left-toolbar-expanded;
        }
    }
    &.plone-toolbar-default {
        ul.plone-toolbar-main > li {
            width: @plone-left-toolbar;
        }
    }

    #edit-zone ul.plone-toolbar-main > li > a {
        // this selector, to be most specific
        & > .icon-up,
        & > .icon-down {
            // make them full width, icon in middle.
            width: 100%;
            display: block;
            padding-left: 0;
        }
    }
}

.plone-toolbar-left #edit-zone {
    z-index: 20;

    nav > ul ul {
        display: none;
        height: 100%;
    }

    .plone-toolbar-container {
        height: 100%;
    }
}

.plone-toolbar-top #edit-zone nav > ul ul {
    width: @plone-toolbar-submenu-width;
    height: 0;
    max-height: 0;
}

.plone-toolbar-default #edit-zone {
    /* Short title support */
    .plone-toolbar-title,
    .plone-toolbar-state-title {
        display: none;
    }
}

.plone-toolbar-expanded #edit-zone {
    width: @plone-left-toolbar-expanded;

    nav > ul ul {
        left: @plone-left-toolbar-expanded;
    }

    nav > ul a > [class^="icon"] {
        display: table-cell;
        width: 40px;
        padding-left: 10px;
    }

    nav > ul a > .icon-logout {
        margin: 0;
        padding: 0;
    }

    nav > ul a > span + span {
        width: @plone-left-toolbar-expanded - 40px;
        max-width: @plone-left-toolbar-expanded - 40px; // needed fixed size for word-wrap to work properly.
        text-align: left;
        background: none;
    }

    .plone-toolbar-caret {
        width: 0;
    }

    li:not(.active) a:hover > span {
        background: @plone-toolbar-link;
    }

    /* Short title support */
    .plone-toolbar-short-title {
        display: none;
    }

    /*states*/

    li {
        a.label-state-published:hover > span,
        li a.label-state-external:hover > span {
            background: @plone-toolbar-published-color;

            &:first-child:before {
                color: @plone-toolbar-text-color;
            }
        }

        a.label-state-internally_published:hover > span {
            background: @plone-toolbar-internally-published-color;

            &:first-child:before {
                color: @plone-toolbar-text-color;
            }
        }

        a.label-state-pending:hover > span {
            background: @plone-toolbar-pending-color;

            &:first-child:before {
                color: @plone-toolbar-text-color;
            }
        }

        a.label-state-draft:hover > span,
        li a.label-state-internal:hover > span {
            background: @plone-toolbar-draft-color;

            &:first-child:before {
                color: @plone-toolbar-text-color;
            }
        }

        a.label-state-private:hover > span {
            background: @plone-toolbar-private-color;

            &:first-child:before {
                color: @plone-toolbar-text-color;
            }
        }
    }

    .plone-toolbar-logo {
        width: @plone-left-toolbar-expanded;

        span {
            display: inline;
        }
    }
}

#edit-zone nav > ul > li.active {
    ul {
        display: block;
        min-width: @plone-toolbar-submenu-width;
        width: auto;
    }

    a > span {
        margin-top: -50px;
    }

    > a {
        background: @plone-toolbar-link;

        &:after {
            position: absolute;
            right: 0;
            margin-top: -35px;
            content: "";
            border-top: 10px solid transparent;
            border-right: 10px solid #2a2a2a;
            border-bottom: 10px solid transparent;
        }

        span:before {
            color: @plone-toolbar-text-color !important;
        } /*state icon on white*/

        &.label-state-published,
        &.label-state-external {
            background: @plone-toolbar-published-color;
        }

        &.label-state-internally_published {
            background: @plone-toolbar-internally-published-color;
        }

        &.label-state-pending {
            background: @plone-toolbar-pending-color;
        }

        &.label-state-draft,
        &.label-state-internal {
            background: @plone-toolbar-draft-color;
        }

        &.label-state-private {
            background: @plone-toolbar-private-color;
        }
    }
}

/*plone-toolbar-top*/

.plone-toolbar-top #edit-zone {
    position: fixed;
    width: 100%;
    height: 50px;

    .plone-toolbar-container {
        margin-right: auto;
        margin-left: auto;

        @media (min-width: @plone-screen-sm-min) {
            width: @plone-container-sm;
        }

        @media (min-width: @plone-screen-md-min) {
            width: @plone-container-md;
        }

        @media (min-width: @plone-screen-lg-min) {
            width: @plone-container-lg;
        }
    }

    .plone-toolbar-logo:after {
        top: 20px;
        left: @plone-left-toolbar;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent;
        border-left: 4px solid darken(@plone-toolbar-link, 10%);
    }

    .plone-toolbar-logo:hover:after {
        -webkit-transition: border-top 0.25s;
        transition: border-top 0.35s;
        border-left: 4px solid @plone-toolbar-link;
    }

    .plone-toolbar-logo,
    nav,
    nav > ul {
        display: inline-block;
    }

    nav > ul > li {
        display: inline-block;
        float: left;
        width: @plone-left-toolbar;
    }

    .plone-toolbar-logo {
        position: absolute;
        top: 0;

        img {
            margin-top: 13px;
        }
    }

    nav {
        width: 100%;
        padding-left: @plone-left-toolbar;
    }

    .plone-toolbar-caret {
        top: 43px;
        right: inherit;
        left: 26px;
        padding: 0 !important;
        border-top: 4px solid;
        border-right: 4px solid transparent;
        border-left: 4px solid transparent;
    }

    nav > ul ul {
        top: 50px;
        left: auto;
        overflow-y: auto;
        height: 0;
    }

    nav > ul li.active ul {
        top: auto;
        height: inherit;
        max-height: 1000px; // trick to activate transition
    }

    li.active > a:after {
        right: inherit;
        margin-top: -20px;
        margin-left: 20px;
        border-right: 10px solid transparent;
        border-bottom: 10px solid #2a2a2a;
        border-left: 10px solid transparent;
    }

    #personal-bar-container {
        float: right;
    }

    #plone-toolbar-more-subset {
        float: right;
        background: rgba(0, 0, 0, 0.9);
    }
}

.plone-toolbar-top.plone-toolbar-expanded #edit-zone {
    .plone-toolbar-logo {
        width: @plone-left-toolbar;
    }

    nav > ul > li {
        width: inherit;
        border-right: 1px solid #353535;
    }

    nav > ul a > span + span {
        width: inherit;
        padding: 0 8px 0 0;
    }

    nav > ul a > [class^="icon"] {
        padding-left: 0;
    }

    .plone-toolbar-caret {
        left: 16px;
    }

    li.active > a:after {
        margin-left: 10px;
    }

    #contentview-history {
        max-width: 135px;
    }
}

/*container*/
/*moved to barceloneta grid.plone.less, needs to match grid system used on theme*/

/*switcher*/

#edit-zone .plone-toolbar-switcher {
    position: absolute;
    top: 0;
    width: 10px;
    height: 10px;
    cursor: pointer;
    border-radius: 0 0 100% 0;
    background: rgba(0, 0, 0, 0.2);

    span {
        font-size: 0;
    }
}

// Space left by the body for the toolbar

.plone-toolbar-left-default {
    padding-left: @plone-left-toolbar;
}

.plone-toolbar-left-expanded {
    padding-left: @plone-left-toolbar-expanded;
}

.plone-toolbar-top-default {
    margin-top: @plone-left-toolbar;
}

.plone-toolbar-top-expanded {
    margin-top: @plone-left-toolbar;
}

/* NOT mobile styles here */
@media (min-width: @plone-screen-xs-max) {
    .plone-toolbar-main {
        overflow-y: auto;
    }

    .plone-toolbar-left #edit-zone {
        z-index: 20;

        nav {
            overflow-x: hidden;
        }

        nav > ul ul {
            width: 0;
            height: 100%;
            background-color: transparent;

            > * {
                background-color: @plone-toolbar-submenu-bg;
            }

            .plone-toolbar-submenu-header {
                margin: 0;
            }
        }

        .plone-toolbar-container {
            position: relative;
            height: 100%;
        }

        #personal-bar-container {
            position: absolute;
            bottom: 0;
            border-top: 1px solid #ddd;
            background-color: black;
            box-shadow: -6px 2px 6px 2px #ddd;

            ul {
                position: fixed;
                top: 0;
                height: 100%;
            }
        }

        .plone-toolbar-main {
            width: @plone-left-toolbar-expanded+18px; /* hide scroll bar */
            padding-right: 18px;
        }
    }

    .plone-toolbar-left-default #edit-zone {
        .plone-toolbar-main {
            width: @plone-left-toolbar+18px; /* hide scroll bar */
        }
    }
}

/*mobile*/
@media (max-width: @plone-screen-xs-max) {
    #edit-zone,
    .plone-toolbar-expanded #edit-zone {
        .plone-toolbar-logo {
            position: absolute;
            left: -60px;
            width: @plone-left-toolbar;

            &:after {
                content: none;
            }

            img {
                margin-top: 12px;
            }
        }
    }

    #edit-zone {
        .plone-toolbar-expanded
            #edit-zone(); /*default view users left-expanded as a reference*/
        /*width: 100%;*/

        right: 0;
        left: inherit;

        nav ul ul {
            width: 200px;
        }

        nav {
            overflow-y: auto;
        }
    }

    body.userrole-authenticated {
        right: 0;
        left: inherit;
        width: 100%;
        height: 100%;
    }

    #edit-zone nav ul li ul {
        width: 0;
    }

    #edit-zone nav ul li.active ul {
        min-width: @plone-toolbar-submenu-width;
        width: auto;
    }

    .plone-toolbar-left-default {
        padding-left: 0;
    }

    .plone-toolbar-left-expanded {
        padding-left: 0;
    }

    .plone-toolbar-top-default {
        margin-top: 0;
    }

    .plone-toolbar-top-expanded {
        margin-top: 0;
    }

    .plone-toolbar-container nav {
        overflow-y: scroll;
        height: 100%;
    }
}
@media (min-width: @plone-screen-xs-min) {
}

